    /*
http://stackoverflow.com/questions/826782/how-to-disable-text-selection-highlighting-using-css
      */
.button{
    background-color: red;
    width: 60px;
    height: 45px;
    margin: 10px 10px 10px 14px;
    float: left;
    border-radius: 10px;
    
   -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    cursor: pointer;
    cursor: hand;

}
.bigButton {
    background-color: red;
    width: 100px;
    height: 45px;
    margin: 10px 10px 10px 14px;
    float: left;
    border-radius: 10px;
}
div#steps {
    font-size: 50px;
    padding: 5px 10px 5px 10px;
/*    word-wrap: break-word;*/
}
#keyboardShortcuts {
    float: left;
    margin-left: 80px;
    color: black;
    font-size: 14px;
}



